import './MyLabel.css';

export interface MyLabelProps {
	/**
	 * Texto por defecto
	 */
	label: string;
	/**
	 * TamaÃ±o por defecto del texto
	 */
	size: 'normal' | 'h1' | 'h2' | 'h3';

	allCaps: boolean;
}

export const MyLabel = ({
	label = 'No Label',
	allCaps = false,

	size = 'normal',
}: MyLabelProps) => {
	return (
		<span className={`${size}`}>
			{allCaps === true ? label.toUpperCase() : label}
		</span>
	);
};
